<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <section class="section">
        <div class="slds-form-element slds-border_bottom">
          <span class="slds-form-element__label">WeChat Message Activity URL</span>
          <div class="slds-form-element__control">
            <span class="slds-form-element__static static-url-value">{{ url.WeChatMessageActivityUrl }}</span>
            <copy-button
              :message="url.WeChatMessageActivityUrl"
              target-name="link"
            >
            </copy-button>
          </div>
        </div>
        <div class="slds-form-element slds-border_bottom">
          <span class="slds-form-element__label">WeChat Notification Activity URL</span>
          <div class="slds-form-element__control">
            <span class="slds-form-element__static static-url-value">{{ url.WeChatNotificationActivityUrl }}</span>
            <copy-button
              :message="url.WeChatNotificationActivityUrl"
              target-name="link"
            >
            </copy-button>
          </div>
        </div>
        <div class="slds-form-element slds-border_bottom">
          <span class="slds-form-element__label">WeChat Broadcast Activity URL</span>
          <div class="slds-form-element__control">
            <span class="slds-form-element__static static-url-value">{{ url.WeChatBroadcastActivityUrl }}</span>
            <copy-button
              :message="url.WeChatBroadcastActivityUrl"
              target-name="link"
            >
            </copy-button>
          </div>
        </div>
      </section>
    </div>
  </right-page>
</template>

<script>
import { getMCCustomActivityUrls } from '../../src/api/charket-settings.js'

export default {
  name: 'JourneyBuilderActivities',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Journey Builder Activities'
      },
      url: {}
    }
  },
  created () {
    this.fetchJourneyBuilderUrls()
  },
  methods: {
    async fetchJourneyBuilderUrls () {
      try {
        this.$bus.emit('show-loading')
        this.url = await getMCCustomActivityUrls()
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 28px 30px 20px;
    .static-url-value {
      display: inline;
      width: auto;
      padding-right: 4px;
    }
  }
</style>
